.home8 {
  width: 100vw;
  height: 100vh;
  background-color: #70b9c1;
  background-image: url(../../images/bg.3fb818ce.gif);
  padding: 30px 20px;
  box-sizing: border-box;
}
.home8 .container {
  width: calc(100vw - 40px);
  height: calc(100vh - 60px);
  border: 1px solid #fff;
  padding: 10px;
  box-sizing: border-box;
  -webkit-animation: containerMove 1s ease-in;
          animation: containerMove 1s ease-in;
  position: absolute;
  top: 30px;
  left: 20px;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
}
@-webkit-keyframes containerMove {
  from {
    top: 100vh;
  }
  to {
    top: 30px;
  }
}
@keyframes containerMove {
  from {
    top: 100vh;
  }
  to {
    top: 30px;
  }
}
.home8 .logo-container {
  background-color: #ffffff;
  width: calc(100vw - 80px);
  height: calc(100vw - 80px);
  padding: 10px;
  box-sizing: border-box;
  -webkit-animation: logoMove 1s linear;
          animation: logoMove 1s linear;
  position: absolute;
  top: 26vh;
  border-radius: 50%;
  overflow: hidden;
  margin-left: 20px;
}
.home8 .logo-container .logo-image {
  border: 1px solid #f1bfcf;
  width: calc(100vw - 110px);
  height: calc(100vw - 110px);
  display: inline-block;
  background-image: url(../../images/test.9a939988.jpeg);
  background-size: 100% 100%;
  border-radius: 50%;
}
@-webkit-keyframes logoMove {
  from {
    -webkit-transform: scale(0.2) rotate(180deg);
            transform: scale(0.2) rotate(180deg);
    top: 26vh;
    left: -100vw;
  }
  to {
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
    top: 26vh;
    left: 0;
  }
}
@keyframes logoMove {
  from {
    -webkit-transform: scale(0.2) rotate(180deg);
            transform: scale(0.2) rotate(180deg);
    top: 26vh;
    left: -100vw;
  }
  to {
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
    top: 26vh;
    left: 0;
  }
}
.home8 .title-container-top {
  position: absolute;
  top: 12vh;
  width: 100vw;
  left: 0;
  -webkit-animation: up-to-down 1s linear;
          animation: up-to-down 1s linear;
}
.home8 .title-container-top .title {
  font-family: -webkit-pictograph;
  font-style: normal;
  font-size: 18px;
  margin-bottom: 20px;
}
@-webkit-keyframes up-to-down {
  from {
    -webkit-transform: translateY(-50vw);
            transform: translateY(-50vw);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes up-to-down {
  from {
    -webkit-transform: translateY(-50vw);
            transform: translateY(-50vw);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
