.home8 .photos-container {
  position: absolute;
  top: 20vh;
  left: 15vw;
  width: 70vw;
  height: 70vh;
}
.home8 .photos-container .image-1 {
  margin-top: 4vh;
  border-radius: 1vh;
  width: 42vw;
  height: 27vh;
  background-size: 100% 100%;
  background-image: url(../../images/a.ea23dd84.jpg);
  background-repeat: no-repeat;
  -webkit-animation: rotate 1s linear;
          animation: rotate 1s linear;
  margin-bottom: 30px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.home8 .photos-container .image-2 {
  margin-top: -15vh;
  margin-left: 16vw;
  border-radius: 1vh;
  width: 38vw;
  height: 27vh;
  background-size: 100% 100%;
  background-image: url(../../images/b.360f80c9.jpg);
  background-repeat: no-repeat;
  -webkit-animation: rotate 1s linear;
          animation: rotate 1s linear;
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
  margin-bottom: 30px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.home8 .photos-container .image-3 {
  margin-top: -15vh;
  margin-left: 32vw;
  border-radius: 1vh;
  width: 38vw;
  height: 27vh;
  background-size: 100% 100%;
  background-image: url(../../images/c.32d66213.jpg);
  background-repeat: no-repeat;
  -webkit-animation: rotate 1s linear;
          animation: rotate 1s linear;
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
  margin-bottom: 30px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes left-to-right {
  from {
    -webkit-transform: translate(-100vw) scale(0.2);
            transform: translate(-100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@keyframes left-to-right {
  from {
    -webkit-transform: translate(-100vw) scale(0.2);
            transform: translate(-100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@-webkit-keyframes right-to-left {
  from {
    -webkit-transform: translate(100vw) scale(0.2);
            transform: translate(100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@keyframes right-to-left {
  from {
    -webkit-transform: translate(100vw) scale(0.2);
            transform: translate(100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
