.home1 {
  width: 100vw;
  height: 100vh;
  background-color: #70b9c1;
  background-image: url(../../images/bg.3fb818ce.gif);
  padding: 30px 20px;
  box-sizing: border-box;
}
.home1 .container {
  width: calc(100vw - 40px);
  height: calc(100vh - 60px);
  border: 1px solid #fff;
  padding: 10px;
  box-sizing: border-box;
  -webkit-animation: containerMove 1s ease-in;
          animation: containerMove 1s ease-in;
  position: absolute;
  top: 30px;
  left: 20px;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
}
@-webkit-keyframes containerMove {
  from {
    top: 100vh;
  }
  to {
    top: 30px;
  }
}
@keyframes containerMove {
  from {
    top: 100vh;
  }
  to {
    top: 30px;
  }
}
.home1 .logo-container {
  background-color: #ffffff;
  width: 40vh;
  height: 40vh;
  padding: 10px;
  box-sizing: border-box;
  -webkit-animation: logoMove 1s linear;
          animation: logoMove 1s linear;
  position: absolute;
  top: 26vh;
  border-radius: 50%;
  overflow: hidden;
  margin-left: calc(50vw - 20vh - 25px);
}
.home1 .logo-container .logo-image {
  border: 1px solid #f1bfcf;
  width: 40vh;
  height: 40vh;
  display: inline-block;
  background-image: url(../../images/4.c1393e7f.jpg);
  background-size: 100% 100%;
  border-radius: 50%;
}
@-webkit-keyframes logoMove {
  from {
    -webkit-transform: scale(0.2) rotate(180deg);
            transform: scale(0.2) rotate(180deg);
    top: 26vh;
    left: -100vw;
  }
  to {
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
    top: 26vh;
    left: 0;
  }
}
@keyframes logoMove {
  from {
    -webkit-transform: scale(0.2) rotate(180deg);
            transform: scale(0.2) rotate(180deg);
    top: 26vh;
    left: -100vw;
  }
  to {
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
    top: 26vh;
    left: 0;
  }
}
.home1 .title-container-top {
  position: absolute;
  top: 12vh;
  width: 100vw;
  left: 0;
  -webkit-animation: up-to-down 1s linear;
          animation: up-to-down 1s linear;
}
.home1 .title-container-top .title {
  font-family: -webkit-pictograph;
  font-style: italic;
  font-size: 18px;
  margin-bottom: 20px;
}
.home1 .title-container-top .date {
  font-family: cursive;
  font-weight: 500;
  font-size: 18px;
}
@-webkit-keyframes up-to-down {
  from {
    -webkit-transform: translateY(-50vw);
            transform: translateY(-50vw);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes up-to-down {
  from {
    -webkit-transform: translateY(-50vw);
            transform: translateY(-50vw);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.home1 .arrow-line {
  position: absolute;
  left: 23%;
  top: 15vh;
  width: 54%;
  height: 20px;
  background-size: 100%;
  background-repeat: no-repeat;
  margin-top: 6px;
  background-image: url(data:image/png;base64,);
  -webkit-animation: arrow-line-rotate 1s linear;
          animation: arrow-line-rotate 1s linear;
}
@-webkit-keyframes arrow-line-rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
@keyframes arrow-line-rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
.home1 .title-container-bottom {
  position: absolute;
  width: calc(100vw - 40px);
  top: 72vh;
  font-size: 18px;
  font-family: serif;
  -webkit-animation: right-to-left 1s linear;
          animation: right-to-left 1s linear;
}
.home1 .title-container-bottom .bottom-line {
  width: 54%;
  height: 40px;
  background-size: 100%;
  background-repeat: no-repeat;
  margin-left: 23%;
  margin-top: 6px;
  background-image: url(../../images/line.67f8faac.png);
}
@-webkit-keyframes right-to-left {
  from {
    -webkit-transform: translateX(50vw);
            transform: translateX(50vw);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes right-to-left {
  from {
    -webkit-transform: translateX(50vw);
            transform: translateX(50vw);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
.home1 .flower-right {
  position: absolute;
  width: 100px;
  top: 0;
  right: 0;
  height: 100px;
  background-size: 100px 100px;
  background-image: url(../../images/flower-left-2.ca847c7c.png);
  background-repeat: no-repeat;
}
.home1 .flower-left {
  position: absolute;
  width: 100px;
  bottom: 0;
  left: 0;
  height: 100px;
  background-size: 100px 100px;
  background-image: url(../../images/flower-right-2.2bb0deee.png);
  background-repeat: no-repeat;
}
