.home2 {
  width: 100vw;
  height: 100vh;
  background-color: #70b9c1;
  background-image: url(../../images/bg.3fb818ce.gif);
  padding: 30px 20px;
  box-sizing: border-box;
}
.home2 .container {
  width: calc(100vw - 40px);
  height: calc(100vh - 60px);
  border: 1px solid #fff;
  padding: 10px;
  box-sizing: border-box;
  -webkit-animation: containerMove 1s ease-in;
          animation: containerMove 1s ease-in;
  position: absolute;
  top: 30px;
  left: 20px;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
}
@-webkit-keyframes containerMove {
  from {
    top: 100vh;
  }
  to {
    top: 30px;
  }
}
@keyframes containerMove {
  from {
    top: 100vh;
  }
  to {
    top: 30px;
  }
}
.home2 .logo-container {
  background-color: #ffffff;
  width: calc(100vw - 80px);
  height: calc(100vw - 80px);
  padding: 10px;
  box-sizing: border-box;
  -webkit-animation: logoMove 1s linear;
          animation: logoMove 1s linear;
  position: absolute;
  top: 26vh;
  border-radius: 50%;
  overflow: hidden;
  margin-left: 20px;
}
.home2 .logo-container .logo-image {
  border: 1px solid #f1bfcf;
  width: calc(100vw - 110px);
  height: calc(100vw - 110px);
  display: inline-block;
  background-image: url(../../images/test.9a939988.jpeg);
  background-size: 100% 100%;
  border-radius: 50%;
}
@-webkit-keyframes logoMove {
  from {
    -webkit-transform: scale(0.2) rotate(180deg);
            transform: scale(0.2) rotate(180deg);
    top: 26vh;
    left: -100vw;
  }
  to {
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
    top: 26vh;
    left: 0;
  }
}
@keyframes logoMove {
  from {
    -webkit-transform: scale(0.2) rotate(180deg);
            transform: scale(0.2) rotate(180deg);
    top: 26vh;
    left: -100vw;
  }
  to {
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
    top: 26vh;
    left: 0;
  }
}
.home2 .title-container-top {
  position: absolute;
  top: 12vh;
  width: 100vw;
  left: 0;
  -webkit-animation: up-to-down 1s linear;
          animation: up-to-down 1s linear;
}
.home2 .title-container-top .title {
  font-family: -webkit-pictograph;
  font-style: normal;
  font-size: 18px;
  margin-bottom: 20px;
}
@-webkit-keyframes up-to-down {
  from {
    -webkit-transform: translateY(-50vw);
            transform: translateY(-50vw);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes up-to-down {
  from {
    -webkit-transform: translateY(-50vw);
            transform: translateY(-50vw);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.home2 .flower-right {
  position: absolute;
  width: 100px;
  top: 0;
  right: 0;
  height: 100px;
  background-size: 100px 100px;
  background-image: url(../../images/flower-left-2.ca847c7c.png);
  background-repeat: no-repeat;
}
.home2 .flower-left {
  position: absolute;
  width: 100px;
  bottom: 0;
  left: 0;
  height: 100px;
  background-size: 100px 100px;
  background-image: url(../../images/flower-right-2.2bb0deee.png);
  background-repeat: no-repeat;
}
.home2 .photos-container {
  position: absolute;
  top: 20vh;
  left: 15vw;
  width: 70vw;
  height: 70vh;
}
.home2 .photos-container .row {
  height: 50%;
}
.home2 .photos-container .row .row-image {
  height: 28vh;
}
.home2 .photos-container .row .text {
  height: 7vh;
}
.home2 .photos-container .row .image {
  width: 28vh;
  height: 28vh;
  display: inline-block;
}
.home2 .photos-container .row .image-1 {
  background-size: 100% 100%;
  background-image: url(../../images/6.3898dbee.jpg);
  background-repeat: no-repeat;
}
.home2 .photos-container .row .image-2 {
  background-size: 100% 100%;
  background-image: url(../../images/3.9e99268f.jpg);
  background-repeat: no-repeat;
}
.home2 .photos-container .row-left {
  -webkit-animation: left-to-right 2s linear;
          animation: left-to-right 2s linear;
  text-align: left;
}
.home2 .photos-container .row-left .text-left {
  line-height: 5vh;
}
.home2 .photos-container .row-right {
  -webkit-animation: right-to-left 2s linear;
          animation: right-to-left 2s linear;
  text-align: right;
}
.home2 .photos-container .row-right .text-right {
  line-height: 9vh;
}
@-webkit-keyframes left-to-right {
  from {
    -webkit-transform: translate(-100vw) scale(0.2);
            transform: translate(-100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@keyframes left-to-right {
  from {
    -webkit-transform: translate(-100vw) scale(0.2);
            transform: translate(-100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@-webkit-keyframes right-to-left {
  from {
    -webkit-transform: translate(100vw) scale(0.2);
            transform: translate(100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@keyframes right-to-left {
  from {
    -webkit-transform: translate(100vw) scale(0.2);
            transform: translate(100vw) scale(0.2);
  }
  to {
    -webkit-transform: translate(0) scale(1);
            transform: translate(0) scale(1);
  }
}
@-webkit-keyframes hidden-to-visible {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes hidden-to-visible {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
